.hover-highlight {
  transition: box-shadow 0.5s ease;
  box-shadow: inset 0 0rem 0 $accent;
  &--active {
    color: $accent;
    box-shadow: inset 0 -0.4rem 0 $accent;
  }
  &:hover {
    color: $accent;
    box-shadow: inset 0 -0.4rem 0 $accent;
  }
}



